<template>

  <div class="wrapper">
    <div class="hero">
      <h2>Shopping List</h2>
      <input class="input" type="text" v-model="item" placeholder="Add Your Item Here" />
      <button class="add-btn" @click.prevent="addItem">Add Item</button>
    </div>
    <div class="list-container">
      <div class="item-container" v-for="(item, index) in items" :key="item.id" >{{item}} <div class="icon-wrapper"><i class="fas fa-times" @click.prevent="removeItem(index, item)"></i></div></div>
  </div>
    </div>
    
</template>

<script>



export default {
  name: 'App',
  data() {
    return {
      item: '',
      items: []
    }
  },
  methods: {
    capitalizeFirstLetter: function(string) {
      return string.charAt(0).toUpperCase() + string.slice(1)
    },

    addItem: function(){
      if(this.item != '') {
        let capitalizedItem = this.capitalizeFirstLetter(this.item)
        this.items.push(capitalizedItem);
      this.item = '';
      }
      
    },
    removeItem: function(index) {
      if(confirm('Delete task?')){
        this.items.splice(index, 1);
      } else {
        console.log("Back")
      }
      
    }
  }
  
}
</script>

